<template>
	<div>
		<el-table
				:data="tableData"
				style="width: 100%">
			<el-table-column
					prop="id"
					label="#ID">
			</el-table-column>
			<el-table-column
					prop="title"
					label="标题">
			</el-table-column>
			<el-table-column
					prop="username"
					label="发起人">
			</el-table-column>
			<el-table-column
					prop="create_at"
					label="创建时间">
			</el-table-column>
			<el-table-column
					prop="url"
					label="分享链接">
			</el-table-column>
			<el-table-column
					label="操作"
					width="300px">
				<template slot-scope="scope">
					<el-button @click="modify(scope.row.id)"  size="small">修改</el-button>
					<el-button @click="dynamic(scope.row.id)"  type="warning" size="small">新增动态</el-button>
					<el-button @click="prove(scope.row.id)"  type="success" size="small">证明</el-button>
					<el-button @click="order(scope.row.id)" type="primary"  size="small">捐款</el-button>
				</template>
			</el-table-column>
		</el-table>



		<el-dialog title="证明" :visible.sync="dialogProveVisible">
			<el-form :model="proveForm">
				<el-form-item label="与X关系" label-width="80px">
					<el-input v-model="proveForm.relation" placeholder="同事/朋友/亲戚" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="介绍" label-width="80px">

					<el-input v-model="proveForm.introduce" placeholder="亲眼所见........."  autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogProveVisible = false">取 消</el-button>
				<el-button type="primary" @click="doProve()">确 定</el-button>
			</div>
		</el-dialog>

		<el-dialog title="捐款" :visible.sync="dialogOrderVisible">
			<el-form :model="orderForm">
				<el-form-item label="金额" label-width="80px">
					<el-input v-model="orderForm.amount" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="鼓励语" label-width="80px">
					<el-input v-model="orderForm.encourage" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogOrderVisible = false">取 消</el-button>
				<el-button type="primary" @click="doOrder()">确 定</el-button>
			</div>
		</el-dialog>


		<el-dialog title="动态" :visible.sync="dialogDynamicVisible">
			<el-form :model="dynamicForm">
				<el-form-item label="动态描述" label-width="80px">
					<el-input v-model="dynamicForm.title" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="上传图片" label-width="80px">
					<el-upload
							action="/api/auth/upload"
							list-type="picture-card"
							:on-success="handleImageSuccess"
					>
						<i class="el-icon-plus"></i>
					</el-upload>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogDynamicVisible = false">取 消</el-button>
				<el-button type="primary" @click="doDynamic()">确 定</el-button>
			</div>
		</el-dialog>
	</div>
</template>


<script>
  export default {
    data() {
      return {
        tableData: [],
        dialogOrderVisible: false,
        dialogProveVisible: false,
        dialogDynamicVisible: false,
        orderForm: {
          project_id: 0,
          amount: '',
          encourage: '',
		},
        proveForm: {
          project_id: 0,
          relation: '',
          introduce: '',
        },
        dynamicForm: {
          project_id: 0,
					image_list: [],
					title: '',
        },
      }
    },
    methods: {
      loadList() {
        this.$httpGet("admin/projectList", this.form).then((response) => {
          if (response.code === 0) {
            this.tableData = response.data.list
          }
        }).catch(() => {
          this.$message({
            message: '请求服务器异常, 请稍后再试',
            center: true,
            type: "error"
          });
        })
      },
			order(id) {
        this.orderForm.project_id = id
        this.dialogOrderVisible = true
      },
      prove(id) {
        this.proveForm.project_id = id
        this.dialogProveVisible = true
      },
      dynamic(id) {
        this.dynamicForm.project_id = id
        this.dialogDynamicVisible = true
      },
      modify(id) {
				this.$store.commit('setProjectId', id)
        this.$router.push('/project/add')
      },
      doOrder() {
        this.$httpPost("admin/orderAdd", this.orderForm).then((response) => {
          if (response.code === 0) {
            this.$message({
              message: '新增成功',
              center: true,
              type: "success"
            });
            this.dialogOrderVisible = false
            this.orderForm = {}
          } else {
            this.$message({
              message: response.msg,
              center: true,
              type: "warning"
            });
					}
        }).catch(() => {
          this.$message({
            message: '请求服务器异常, 请稍后再试',
            center: true,
            type: "error"
          });
        })
      },
      doProve() {
        this.$httpPost("admin/proveAdd", this.proveForm).then((response) => {
          if (response.code === 0) {
            this.$message({
              message: '新增成功',
              center: true,
              type: "success"
            });
            this.dialogProveVisible = false
            this.proveForm = {}
          } else {
            this.$message({
              message: response.msg,
              center: true,
              type: "warning"
            });
          }
        }).catch(() => {
          this.$message({
            message: '请求服务器异常, 请稍后再试',
            center: true,
            type: "error"
          });
        })
      },
      doDynamic() {
        this.$httpPost("admin/dynamicAdd", this.dynamicForm).then((response) => {
          if (response.code === 0) {
            this.$message({
              message: '新增成功',
              center: true,
              type: "success"
            });
            this.dialogDynamicVisible = false
            this.dynamicForm = {}
          } else {
            this.$message({
              message: response.msg,
              center: true,
              type: "warning"
            });
          }
        }).catch(() => {
          this.$message({
            message: '请求服务器异常, 请稍后再试',
            center: true,
            type: "error"
          });
        })
      },
			// eslint-disable-next-line no-unused-vars
      handleImageSuccess(res, file){
        if(res.code === 0) {
          this.dynamicForm.image_list.push(res.data.file)
        }
      },
    },
	activated() {
      this.loadList()
    }
  }
</script>

<style scoped>

</style>